.banner {
    width: 100%;
    height: 200px;
    margin-top: 50px;
    background: rgb(131,58,180);
    background: linear-gradient(90deg, var(--purple) 0%, var(--blue) 100%);
    text-align: center;
    box-shadow:  0px 12px 12px #21252b;
}

.banner h1 {
    color: #ffffff;
    font-weight: bold;
    font-size: 2rem;
    padding-top: 70px;
    padding-bottom: 10px;
}

.banner p {
    font-size: 0.8rem;
}

.main {
    max-width: 960px;
    padding: 50px 30px;
    margin: 2px auto;
}

.main h1 {
    color: #fdfdfd;
    font-size: calc(4vmin + 10px);
    margin-top: 10px;
    /* background: linear-gradient(to right top, #861657, #ffa69e);
    color: transparent;
    -webkit-background-clip: text; */
}

.highlight {
    /* background-color: #8a9095; */
}

.gradient {
    /* background: linear-gradient(to right top, #861657, #ffa69e); */
}

section {
}

.main p {
    color: white;
    font-size: 1rem;
    padding-top: 15px;
    padding-bottom: 15px;
}

.main a {
    font-weight: bold;
}

.main button {
    background-color: var(--purple);
    border: none;
    color: white;
    padding: 8px 16px;
    border-radius: 5px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    transition-duration: 0.4s;
    font-weight: bold;
}

.main button:hover {
    background-color: var(--blue);
}

.ranking {
    max-width: 960px;
    padding: 50px 30px;
    margin: 2px auto;
}

.ranking h1 {
    color: #ffffff;
    font-weight: bold;
    font-size: 1.7rem;
    padding-bottom: 15px;
}

.ranking .box {
    max-width: 200px;
    width: 100%;
    border-radius: 20px;
    box-sizing: border-box;
    padding: 20px;
    margin-right: 20px;
    background-color: var(--light-bg-color)
}

.ranking .box h1 {
    color: #ffffff;
    font-weight: bold;
    font-size: 1rem;
    padding-bottom: 15px;
}

.ranking .box p {
    font-weight: normal;
    font-size: 0.9rem;
    line-height: 1.2;
}

footer {
    background-color: var(--dark-bg-color)
}

.footer {
    max-width: 960px;
    height: 200px;
    padding: 30px 30px;
    margin: 2px auto;
    font-size: 12px;
}

.footer p {
    padding-bottom: 10px;
}